import './App.css';
import React, { Component } from 'react'
import Header from './Components/Header/Header'
import Home from './Components/Home/Home'
import Display from './Components/Display/Display'
import Footer from './Components/Footer/Footer';
import Search from './Components/Search/Search';
import NoResult from './Components/NoSesult/NoResult';
import Papa from 'papaparse';

//import { HashRouter, Route } from 'react-router-dom';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [], 
      userInput: '', 
      showHouseInfo: false, 
      houseData: null,
      fileLoaded: false, // 用于判断是否成功打开CSV文件
      loading: true // 用于判断是否正在加载文件
    };
  }

  componentDidMount() {
    Papa.parse('/source/clean.csv', {
      download: true,
      header: true,
      complete: (result) => {
        this.setState({ data: result.data });
      },
      error: (error) => {
        this.setState({ loading: false });
        console.error('文件加载失败:', error);
      }
    });
  }

  handleInputChange = (e) => {
    this.setState({ userInput: e.target.value });
  };

  handleSearch = () => {
    const { userInput, data } = this.state;
    const houseData = data.find(item => item.name === userInput);
    
    if (houseData) {
      this.setState({ showHouseInfo: true, houseData });
    } else {
      this.setState({ showHouseInfo: false, houseData: null });
    }
  };

  render() {
    const { showHouseInfo, houseData, fileLoaded, loading } = this.state;

  
    return (
      <div>
        <Header
          onInputChange={this.handleInputChange}
          onSearch={this.handleSearch}
        />
        {/* {showHouseInfo ? <Search data={houseData} /> : <NoResult />} */}
      
        <Home />
        <Display />
        <Footer />


        {/* <HashRouter>
      <Route path = "/search" component = {Search}/>
    </HashRouter> */}
      </div>

    )
  }
}
