import React, { Component } from 'react'
import './Home.css'
import R from '../../image/H.jpg'
// import videoSrc from './video.mp4';
// import p_black from '../../image/black.png'


export default class Home extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
          texts: ["price analyze", "price predict", "display house information"],
          textIndex: 0,
          charIndex: 0,
          isDeleting: false,
          typingSpeed: 200,
        };
      }

      componentDidMount() {
        this.typeText();
      }

      typeText = () => {
        const { texts, textIndex, charIndex, isDeleting, typingSpeed } = this.state;
        const typewriter = document.getElementById('writer');
    
        const text = texts[textIndex];
    
        if (isDeleting) {
          typewriter.textContent = text.substring(0, charIndex - 1);
        } else {
          typewriter.textContent = text.substring(0, charIndex + 1);
        }
    
        this.setState((prevState) => ({
          charIndex: isDeleting ? prevState.charIndex - 1 : prevState.charIndex + 1,
        }));
    
        if (isDeleting && charIndex === 0) {
          this.setState((prevState) => ({
            isDeleting: false,
            textIndex: (prevState.textIndex + 1) % texts.length,
          }));
        }
    
        if (!isDeleting && charIndex === text.length) {
          this.setState({
            isDeleting: true,
            typingSpeed: 200,
          });
        }
    
        setTimeout(this.typeText, typingSpeed);
      };

    render() {
        return (
            <div id='Div'>
                {/* <video autoPlay loop muted id='video'>
                        <source src={videoSrc} type="video/mp4" />
                </video> */}

                {/* <img id='black' src={p_black} alt=""/> */}

                <section className='home'>
                    <div className='home_content'>
                        <h3>Hello, It's Me,</h3>
                        <h1>House Price Prediction</h1>
                        <h3>And I can&nbsp; 
                            <div id='writer-container'>
                               <div id="writer"></div>
                               <div id="cursor"></div>
                            </div>
                        </h3>
                        <div className='social-media'>
                          <a href='#' ><i className='bx bxl-tiktok'></i></a>
                          <a href='#' ><i className='bx bxl-facebook-circle'></i></a>
                          <a href='#' ><i className='bx bxl-google'></i></a>
                          <a href='#' ><i className='bx bxl-linkedin-square'></i></a>
                        </div>
                        <a href='#' className='btn'>Learn More</a>
                    </div>

                    
                        <a href='http://wjbaby.housescheck.com/' alt="" id='imgA'><img src={R} alt='' className='home_img'/></a>
                    
                </section>
            </div>

        )
    }
}

